.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 19px auto;
  font-size: 16px;
  line-height: 18.4px;
  font-family: "Arial", sans-serif;

  @media (min-width: $tablet-min) {
    margin: 0;
  }
}

.copyright__link {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $special_dark-grey;

  &:hover .copyright__img {
    fill: $basic_green;
  }

  &:active {
    opacity: 0.3;
  }
}

.copyright__img {
  fill: $special_grey-2;
}
