.social {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-min) {
    margin-left: 19px;
    border: none;
  }

  @media (min-width: $tablet-min) {
    margin-left: 19px;
  }
}

.social__list {
  display: flex;
  width: 137px;
  height: 63px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $tablet-min) {
    width: 129px;
    height: auto;
  }
}

.social__item {
  display: flex;
  height: 34px;
  width: 31px;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  fill: #666666;

  &--vk svg {
    width: 23px;
    height: 16px;

    @media (min-width: $tablet-min) {
      width: 30px;
      height: 22px;
    }
  }

  &--inst svg {
    width: 16px;
    height: 16px;

    @media (min-width: $tablet-min) {
      width: 23px;
      height: 23px;
    }
  }

  &--facebook svg {
    width: 22px;
    height: 22px;

    @media (min-width: $tablet-min) {
      width: 28px;
      height: 33px;
    }
  }

  &:hover,
  &:focus,
  &:active {
    fill: $basic_green;
  }

  &:active {
    opacity: 0.3;
  }
}
