.catalog-title {
  margin: 0 auto;
  padding-top: 27px;
  padding-bottom: 41px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  text-align: center;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;

  @media (min-width: $tablet-min) {
    max-width: 708px;
    padding: 0;
    border: none;
    text-align: left;
    margin-top: 65px;
    margin-bottom: 83px;
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: $desktop-min) {
    max-width: 1220px;
    margin-top: 74px;
    margin-bottom: 84px;
  }
}
