.gift {
  height: 289px;
  background-color: grey;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/gift-photo-mobile@1x.webp");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/gift-photo-mobile@2x.webp");
  }

  @media (min-width: $tablet-min) {
    display: flex;
    align-items: center;
    height: 200px;
    padding-left: 58px;
    padding-right: 124px;
    background-image: url("../img/gift-photo-tablet@1x.webp");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-tablet@2x.webp");
    }
  }

  @media (min-width: $desktop-min) {
    padding: 0;
    justify-content: center;
    height: 100%;
    background-image: url("../img/gift-photo-desktop@1x.webp");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-desktop@2x.webp");
    }
  }
}

.gift__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;

  @media (min-width: $tablet-min) {
    flex-direction: row;
    height: auto;
    width: 100%;
  }

  @media (min-width: $desktop-min) {
    flex-direction: column;
    width: auto;
    align-items: center;
    justify-content: space-between;
    margin-top: 56px;
    margin-bottom: 42px;
  }
}

.gift__img {
  margin-top: 58px;

  @media (min-width: $tablet-min) {
    margin: 0;
  }
}

.gift__text {
  margin: 0;
  width: 161px;
  color: white;
  font-size: 16px;
  line-height: 20px;
  font-family: "Arial", sans-serif;
  text-align: center;
  margin-top: auto;
  margin-bottom: 43px;

  @media (min-width: $tablet-min) {
    margin-bottom: 0;
    text-align: left;
  }

  @media (min-width: $desktop-min) {
    margin: 0;
    margin-top: 46px;
    text-align: center;
  }
}
